.testimonial-container {
  padding: 30px 0 70px 0;
  margin-top: 3rem;
}
.testimonial-container .section-title h2 {
  color: #454545;
  letter-spacing: 3px;
  margin-bottom: 30px;
}
.testimonial-container .fade-testimonial-container {
  width: 100%;
  margin: 0 auto;
}
.testimonial-container .each-fade-effect {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 30vh;
  text-align: center;
  border: 1px solid var(--main-color);
  border-radius: 3px;
  padding: 10px 20px;
}
.testimonial-container .each-fade-effect p {
  margin-bottom: 0;
  color: #6f6f6f;
  font-weight: bold;
  letter-spacing: 1px;
  margin-right: 20px;
}
.testimonial-container .each-fade-effect .testimonial-image-container {
  margin-left: 20px;
  margin-right: 10px;
}
.testimonial-container .each-fade-effect .testimonial-image-container img {
  border-radius: 50%;
  object-fit: cover;
  width: 80px;
  height: 80px;
}
.react-slideshow-container + ul.indicators {
  padding-inline-start: 0 !important;
}
.react-slideshow-container + ul.indicators li {
  margin-right: 10px;
}
.react-slideshow-container + ul.indicators .each-slideshow-indicator:before {
  height: 10px !important;
  width: 10px !important;
  background: var(--main-color) !important;
}

@media only screen and (min-width: 768px) {
  .testimonial-container .fade-testimonial-container {
    width: 70%;
  }
}

@media only screen and (min-width: 600px) {
  .testimonial-container .each-fade-effect {
    height: 20vh;
  }
}

@media only screen and (max-width: 480px) {
  .testimonial-container .fade-testimonial-container {
    width: 90%;
  }
  .testimonial-container .section-title h2 {
    margin-bottom: 15px;
  }
  .testimonial-container .each-fade-effect {
    height: 35vh;
    flex-direction: column;
  }
  .testimonial-container .each-fade-effect .testimonial-image-container img {
    margin-bottom: 10px;
  }
}

@media only screen and (max-width: 360px) {
  .testimonial-container .each-fade-effect {
    height: 50vh;
  }
}
